
import Divider from 'vux/src/components/divider/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: '',
components: {
Divider,
Icon,
Cell,
XNumber,
Group,
Toast
},
destroyed () { // 销毁时显示
this.componentsShow(true)
},
mounted () { // 隐藏
this.componentsShow(false)
},
data () {
return {
len: 1,
toastData: {
isShow: false,
type: 'warn',
text: '已成功删除'
},
checked: true,
nofreight: '',
lists: [],
amouts: 0
}
},
async created () {
this.nofreight = sessionStorage.getItem('nofreight')
this.getLists()
},
methods: {
componentsShow (bool) {
this.$store.state.bottomShow = bool
},
checkAll () { // 全选
this.checked = !this.checked
this.lists.map((item, index) => {
item.checked = this.checked
})
this.accounts(this.lists) 
},
check (item, index) { // 单选
this.lists.some((item1, index1) => {
if (index === index1) {
item1.checked = !item1.checked
return true
}
})
let bool = this.lists.every((item1, index1) => { // 全选
return item1.checked
})
this.checked = bool
this.accounts(this.lists) 
},
changeNumber (index, id) { // 增减
console.log(this.lists)
this.accounts(this.lists) 

},
async getLists () {
let result = await this.axios.post('cart/cartlist')
if (result.success) {
result.data.map((item, index) => {
item.checked = true
item.img = this.base_img + item.propic
})
this.lists = result.data
this.len = result.data.length
this.accounts(result.data) 
}
},
accounts (lists) { // 计算总额 proamount * number
this.amouts = 0
lists.map((item) => {
if (item.checked) {
this.amouts += Number(item.proamount) * Number(item.number)
}
})
},
settle () { // 结算
let settleLists = [] 
this.lists.map((item, index) => {
if (item.checked) {
settleLists.push(item)
}
})
if (settleLists.length > 0) {
settleLists = JSON.stringify(settleLists)
console.log(settleLists)
sessionStorage.setItem('settleLists', settleLists)
this.$router.push({name: 'Settle', params: {type: 3}})
} else {
this.toastData = {
isShow: true,
type: 'warn',
text: '请选择商品'
}
}
},
async del (index, id) { // 删除
let _this = this
let result = await this.axios.get('cart/cartdel/' + id)
if (result.success) {
this.toastData.isShow = true
let timer = setTimeout(() => {

_this.lists.some((item1, index1) => {
if (item1.id === id) {
_this.lists.splice(index1, 1)
}
})
_this.accounts(_this.lists) 
clearTimeout(timer)
}, 100)
}
},
async addNum (index, id) { // 加
let result = await this.axios.get('cart/cartnumberadd/' + id)
if (result.success) {
}
},
async subNum (index, id) { // 减
let result = await this.axios.get('cart/cartnumbersub/' + id)
if (result.success) {
}
}
}
}



